import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import firstpage_web from './images/firstpage_web.jpg';
import firstpage_anim from './images/firstpage_anim.jpg';
import firstpage_multi from './images/firstpage_multi.jpg';
import firstpage_print from './images/firstpage_print.jpg';



class Firstpage extends Component {
  render() {
    return (
        <div className="firstPage">
            <div className="mainContent">
                <div className="hamburger-menu anim">
                    <div className="bar"></div>	
                </div>
                <nav className="mainNav">
                    <div className="rotate"></div>
                    <div className="rotate"></div>
                    <div className="rotate"></div>
                    <div className="rotate"></div>
                    <ul>
                        <li className=""><a href="">What I do?</a></li>
                        <li className=""><a href="">Who am I?</a></li>
                        <li className=""><a href="">Where 2 find me?</a></li>
                    </ul>
                </nav>
                <div className="title">
                    <h1>Amir Shahbabaee</h1>
                    <h2>Designer . Developer</h2>
                </div>
                <a className="scroller" href="#section2"><span></span></a>
            </div>
            <div className="horizontalLineBreak"></div>
            <div className="mainContent" id="section2">
                <a className="firstpage_Wrapper">
                    <img src={firstpage_web}  />
                </a>
                <a className="firstpage_Wrapper">
                    <img src={firstpage_anim}  />
                </a>
                <a className="firstpage_Wrapper">
                    <img src={firstpage_multi}  />
                </a>
                <a className="firstpage_Wrapper">
                    <img src={firstpage_print}  />
                </a>
            </div>
        </div>
    );
  }
}

export default Firstpage;






